@import './variables/colors.scss';

body {
  font-family: 'Lato', sans-serif !important;
  margin: 0;
  overflow-x: hidden;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

img {
  height: auto;
  width: 100%;
}

.container {
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
}

.tag {
  padding: 4px 16px;
  height: 28px;
  width: 124px;

  &.purple {
    background-color: $indigo;
  }

  i {
    margin-left: 4px;
    margin-right: 2px;
  }
}

section {
  .heading {
    margin-bottom: 20px;

    .title {
      font-size: 1.3em;
      font-weight: 700;
      text-transform: uppercase;

      span {
        font-weight: 400;
      }
    }
  }
}

.temperature,
.extra {
  .item {
    &-circle {
      border-radius: 50%;
      height: 10px;
      margin: 0 8px;
      width: 10px;

      &.pink {
        background-color: $pink;
      }

      &.orange {
        background-color: $orange;
      }

      &.blue {
        background-color: $blue;
      }
    }
  }
}

.temperature {
  h2 {
    font-size: 4.4em;
    font-weight: 300;
  }
}

.carousel {
  &-heading {
    align-items: center;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding: 10px 0;
    margin-bottom: 8px;

    &-section {
      h5 {
        font-size: 1.1em;
        text-transform: uppercase;
      }
    }
  }

  &-buttons {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: absolute;
  }

  &-slider {
    &-content {
      margin: 0;
      padding: 0;
      list-style: none;
      width: 100%;
      display: flex;
      position: absolute;
      left: 0;
      transition: all 1s ease;

      .item {
        &-slide {
          align-items: center;
          display: flex;
          border-right: 1px solid $steel;
          flex-direction: column;
          justify-content: flex-start;
          height: 110px;

          h3 {
            margin-bottom: 12px;
          }

          h2 {
            font-size: 1.8em;
            font-weight: 300;
            margin-bottom: 24px;
          }
        }
      }

      @media (min-width: 768px) {
        .item {
          &-slide {
            align-items: center;
            display: flex;
            border-right: 1px solid $steel;
            flex-direction: column;
            justify-content: flex-start;
            min-width: 32%;
            height: 110px;
            padding: 1em 0;
            padding-left: 0.88em;
  
            h3 {
              margin-bottom: 12px;
            }
  
            h2 {
              font-size: 1.8em;
              font-weight: 300;
              margin-bottom: 24px;
            }
          }
        }
      }
      
      @media (min-width: 316px) {
        .item {
          &-slide {
            min-width: 29.9%;
            padding: 1em;
          }
        }
      }
      
      @media (min-width: 1200px) {
        .item {
          &-slide {
            min-width: 244px;
            padding: 1em 0.88em;
          }
        }
      }
    }
  }
}

.extra {
  align-items: center;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  margin-bottom: 10px;

  h6 {
    font-size: 16px;
    font-weight: 300;
  }
}

.radious {
  &-10 {
    border-radius: 30px;
  }

  &-20 {
    border-radius: 30px;
  }

  &-30 {
    border-radius: 30px;
  }

  &-40 {
    border-radius: 40px;
  }

  &-50 {
    border-radius: 50%;
  }
}

.card {
  box-shadow: 0 3px 6px $shadow;
}

.color {
  &--white {
    color: $white;
  }

  &--dark {
    color: $dark;
  }

  &--purple {
    color: $purple;
  }

  &--gray {
    color: $gray;
  }
}

button {
  background-color: $white;
  border: 1px solid $indigo;
  color: $indigo;
  cursor: pointer;
  height: 40px;
  width: 40px;
  margin-left: 14px;
  transition: 0.2s all ease;

  &:hover {
    background-color: $indigo;
    border: 1px solid $indigo;
    color: $white;
  }

  &:focus {
    outline: none !important;
  }
}

@media (min-width: 768px) {
  .container {
    width: 750px;
  }
}

@media (min-width: 992px) {
  .container {
    width: 970px;
  }
}

@media (min-width: 1200px) {
  .container {
    width: 1170px;
  }
}