.dashboard {
    align-content: center;
    display: flex;
    flex-direction: row;
    justify-content: center;
    padding: 40px 0;
    width: 100%;

    main {
        padding: 0 10px;
    }

    aside {
        padding: 0 10px;
    }

    @media (min-width: 316px) {
        main {
            width: auto;
        }
    
        aside {
            width: auto;
        }
       
    }

    @media (min-width: 1200px) {
        main {
            width: 70%;
        }
    
        aside {
            width: 30%;
        }
    }
}

@media (min-width: 316px) {
    .dashboard {
        align-content: center;
        flex-direction: column;
        justify-content: center;
    }
   
}

@media (min-width: 1200px) {
    .dashboard {
        align-content: center;
        flex-direction: row;
        justify-content: center;
    }
}